import api from "../../../../utils/api.js";
import util from "../../../../utils/util.js";
import upload from "../../../../components/upload.vue";
import uniNumberBox from "@/components/uni-number-box/uni-number-box.vue";
import dialog from "@/components/dialog.vue";
export default {
	components: {
		upload,uniNumberBox,dialog
	},
	data() {
		return {
			totalCost: 0,     // 合计费用
			result_num:0,     // 物料数量
			result_big_num:0, // 大货数量
			orderItem: '',
			index: 1,
			upLoadMaxNum:9,
			files:[],
			id: '',
			detailData:'',
			result_desc:'',  // 物料描述
			big_price:0,     // 大货单价
			result_price:0,         // 物料单价
			supplier:'',     // 供应商
			list: [       //要展示的数据
				
			],
			supplier_id:'',
			isCheckSupplier: false, 
			formShow:false,
			remark: '',    // 找不到物料原因
			shipping_extra_price:0,
			
		};
	},
	onLoad(options) {
		this.$data.id = options.id;
		this.$data.index = options.index;
		if(options.index == 1){
			uni.setNavigationBarTitle({
				title: "找料订单详情"
			})
		}else{
			uni.setNavigationBarTitle({
				title: "取送订单详情"
			})
		}
		
		this.getOrderDetail(options.id);
		this.getTaskFee();
	},
	onShow() {

	},
	methods: {
		// 联系用户
		contact(item){
			let data={
				id:item.id,
				type:1
			}
			api.apiPhoneStaff({
				method:'POST',
				data
			}).then((res)=>{
				
				if(res.code == 200 || res.code == 0){
					uni.makePhoneCall({
						phoneNumber: res.data
					})
				}else{
					util.errorTips(res.msg);
				}
			}).catch((res)=>{
				util.errorTips(res.msg)
			})
		},
		// 取聊天室
		goChat(item){
			uni.navigateTo({
				url:'/pages/chat/chat?fromUserId='+uni.getStorageSync('userInfo').id+'&toUserId=' + item.user_id + '&name=客户'
			})
		},
		// 确认接单
		receiptOrder(id){
			let _this = this;
			uni.showModal({
				title: '提示',
				content: '确定接单?',
				success: function (res) {
					if (res.confirm) {
						console.log('用户点击确定');
						api.staffFindGet({
							method:'POST',
							data:{
								id
							}
						}).then((res)=>{
							if(res.code == 200 || res.code == 0){
								_this.$data.status = 2; // 状态2 已接单
								util.successTips("接单成功");
								uni.navigateBack({
									delta:1
								})
							}
						})
					} else if (res.cancel) {
						console.log('用户点击取消');
					}
				}
			});
			
		},
		showForm(){
			this.$data.formShow = true;
		},
		
		unFormSubmit(){
			if(this.$data.remark == ""){
				util.errorTips('请填写未找到料的原因');
				return false;
			}
			let data = {
				id:this.$data.id,
				remark:this.$data.remark
			}
			api.staffFindUnfound({
				method:'POST',
				data
			}).then((res)=>{
				if(res.code == 200 || res.code == 0){
					this.$data.formShow = false;
					uni.navigateBack({
						delta:1
					})
				}else{
					util.errorTips(res.msg)
				}
			}).catch((res)=>{
				util.errorTips(res.msg || res.message)
			})
		},
		// 关闭弹窗
		close() {
			this.$data.formShow = false;
		},
		trim(str){
			    if(str == null){  
					str = "";  
				}  
				return str.replace(/\s/g, ""); 
		},
		// 提交信息
		submit(){
			if(this.$data.result_desc == ""){
				util.errorTips('描述不能为空');
				return false;
			}
			if(this.$data.files.length<=0){
				util.errorTips('至少上传一张图片');
				return false;
			}
			if(this.$data.supplier_id == ''){
				util.errorTips('请选择供应商');
				return false;
			}
			if(this.$data.result_price == ""){
				util.errorTips('物料单价不能为空');
				return false;
			}
			if(this.$data.result_num<=0){
				util.errorTips('物料数量不能为空');
				return false;
			}
			let result_img = [];
			this.$data.files.forEach((o,i)=>{
				result_img.push(o.url)
			})
			
			let data = {
				id:this.$data.id,
				supplier_id:this.$data.supplier_id,
				result_desc: this.trim(this.$data.result_desc),
				result_img,
				result_price:this.$data.result_price,
				result_num:this.$data.result_num,
				result_big_num:this.$data.result_big_num
			}
			
			api.staffFindFound({
				method:'POST',
				data
			}).then((res)=>{
				if(res.code == 200 || res.code ==0){
					uni.navigateBack({
						delta:1
					})
				}else{
					util.errorTips(res.msg);
				}
			}).catch((res)=>{
				util.errorTips(res.msg);
			})
			
		},
		checkSupplier(index){
			this.$data.supplier = this.$data.list[index].name;
			this.$data.supplier_id = this.$data.list[index].id;
			this.$data.isCheckSupplier = false;
		},
		sum(){
			this.$data.totalCost = this.$data.result_num * this.$data.result_price + this.$data.result_big_num * this.$data.big_price;
		},
		staffSupplierSearch(e){
			this.$data.supplier_id = '';
			if(e.detail.value != ""){
				api.staffSupplierSearch({
					data:{
						keyword:e.detail.value
					}
				}).then((res)=>{
					let arr = [];
					if(res.code == 200 || res.code == 0){
						this.$data.list = res.data;
						if(res.data.length<=0){
							this.$data.isCheckSupplier = false;
						}else{
							this.$data.isCheckSupplier = true;
						}
						
					}
				})
			}
			
		},
		getPriceByOne(e){
			this.$data.result_price = e.detail.value;
			this.sum();
		},
		bindChange1(e){
			this.$data.result_num = e;
			this.sum();
		},
		bindChange2(e){
			this.$data.result_big_num = e;
			this.sum();
		},
		getTaskFee(){
			api.getTaskFee({}).then((res)=>{
				if(res.code == 200 || res.code == 0){
					this.$data.big_price = res.data.big_price;
					this.$data.shipping_extra_price = res.data.shipping_extra_price;
					this.$data.big_price = res.data.big_price;
					this.sum();
				}
			})
		},
		imageUpload(e) {
			this.$data.files = e.files;
		},
		getOrderDetail(id) {
			api.findShowById({
				data:{id}
			}).then((res)=>{
				if(res.code == 200 || res.code == 0){
					this.$data.detailData = res.data;
				}
			})
		},
		// 预览图片
		preview(e) {
			let idx = e.currentTarget.dataset.idx;
			let imgs = this.data.detailData.desc_img;
			uni.previewImage({
				current: imgs[idx],
				urls: imgs
			})
		},
	},
}
